html {
  font-family: source-han-sans-simplified-c, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 62.5%;
}

.App {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  overflow: hidden;
}

.Fit-width-img {
  object-fit: cover;
  width: 100%;
}

.Cloud-float1 {
  object-fit: cover;
  width: 100%;
  animation: Cloud-float1 1.5s linear 0s infinite alternate;
}

@keyframes Cloud-float1 {
  from {
    top: 20vw;
  }

  to {
    top: 25vw;
  }
}

.Cloud-float2 {
  transform: rotate(5deg);
  animation: Cloud-float2 1.3s linear 0s infinite alternate;
  animation-delay: 333ms;
}

@keyframes Cloud-float2 {
  from {
    top: 60vw;
  }

  to {
    top: 54vw;
  }
}

.Cloud-float3 {
  transform: scaleY(-1) rotate(30deg); 
  animation: Cloud-float3 1.3s linear 0s infinite alternate;
}

@keyframes Cloud-float3 {
  from {
    top: 0vw;
  }

  to {
    top: 7vw;
  }
}

.Cloud-float4 {
  transform: rotate(-10deg);
  animation: Cloud-float4 0.8s linear 0s infinite alternate;
}

@keyframes Cloud-float4 {
  from {
    top: 70vw;
  }

  to {
    top: 60vw;
  }
}

.Plane-fly {
  transform: rotate(-15deg);
  animation: Plane-fly 3s cubic-bezier(.36,.16,.8,.02) infinite;
}

@keyframes Plane-fly {
  from {
    left: -20vw;
    top: 85vw;
    width: 30vw;
  }

  to {
    left: 60vw;
    top: 10vw;
    width: 250vw;
  }
}